import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

import * as React from 'react';
import { Box, Card, Grid, Typography, CircularProgress, CardContent, CardActions, Button } from '@mui/material';
import { gridSpacing } from 'store/constant';

const CustomCard = (props) => (
    <Card sx={{ minWidth: 275 }}>
        <CardContent>
            <Typography gutterBottom variant="h3" component="div">
                {props.title}
            </Typography>
            <Typography variant="body2">{props.meaning}</Typography>
        </CardContent>
        <CardActions>
            <Link to={props.link} state={{ slideMode: true, currentNum: 0, from: 0, to: 20 }} style={{ textDecoration: 'none' }}>
                <Button size="small">{props.buttonText}</Button>
            </Link>
        </CardActions>
    </Card>
);

// ============================|| UTILITIES SHADOW ||============================ //

const UtilitiesShadow = () => (
    <Grid container spacing={gridSpacing} justifyContent={'space-around'}>
        <Grid item lg={4} md={6} sm={6} xs={12}>
            <CustomCard
                title={'Active traning'}
                meaning={'Compare phrases with variants'}
                buttonText={'Start traning'}
                link={'/'}
            ></CustomCard>
        </Grid>
        <Grid item lg={4} md={6} sm={6} xs={12}>
            <CustomCard
                title={'Simple sliding'}
                meaning={'Just slide card'}
                buttonText={'Start traning'}
                link={'/utils/util-color'}
            ></CustomCard>
        </Grid>
    </Grid>
);

export default UtilitiesShadow;
